<template>
  <div class="perSummary">
    <el-header>
      <!--头部用户查询-->
      <el-select v-model="value" filterable @change="getBranchUser"  placeholder="请选择类型">
        <el-option
          v-for="item in options"
          :key="item._id"
          :label="item.branchName"
          :value="item._id">
        </el-option>
      </el-select>
      <!--头部的设置-->
      <el-dropdown>
        <i class="el-icon-setting" style="margin-right: 15px"></i>
        <span>设置</span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>查看</el-dropdown-item>
          <el-dropdown-item>新增</el-dropdown-item>
          <el-dropdown-item>删除</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <!--表格-->
    <el-main>
      <el-table
        :data="tableData"
        stripe
        style="width: 100%"
      >
        <el-table-column
          prop=""
          label="#"
          type="index"
          :index="formatter"
          width="100"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="userName"
          label="用户名"
          width="300"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="branchName"
          label="党支部名称"
          width="300"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <!--查看的弹框-->
            <el-button size="mini" type="primary" @click="FormVisible = true; look(scope.row._id)">查看</el-button>
            <el-dialog title="个人总结及评论详情" :visible.sync="FormVisible" width="80%">
              <el-form :model="formData">
                <el-form-item label="个人总结">
                  <el-upload
                    class="avatar-uploader"
                    action="https://upload-z1.qiniup.com"
                    :disabled="disabled"
                    >
                    <img v-if="formData.pic" :src="formData.pic" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  </el-upload>
                </el-form-item>
              </el-form>
              <el-table :data="dialogTableData">
                <el-table-column
                  prop=""
                  label="#"
                  type="index"
                  :index="formatter"
                  show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                  prop="userName"
                  label="评议人"
                  show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                  prop="branchName"
                  label="党支部"
                  show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                  prop=""
                  label="评分"
                  show-overflow-tooltip
                >
                  <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.content == 0">优</el-tag>
                    <el-tag type="primary" v-if="scope.row.content == 1">良</el-tag>
                    <el-tag type="warning" v-if="scope.row.content == 2">中</el-tag>
                    <el-tag type="danger" v-if="scope.row.content == 3">差</el-tag>
                  </template>
                </el-table-column>
              </el-table>
              <div slot="footer" class="dialog-footer">
                <el-pagination
                  background
                  layout="prev, pager, next"
                  :total="50"
                >
                </el-pagination>
              </div>
            </el-dialog>
            <el-button
              size="mini"
              type="danger"
              @click="del(scope.row._id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
    <!--底部-->
    <el-footer>
      <el-pagination
        background
        layout="prev, pager, next,jumper"
        :total="total"
        :current-page="page"
        :page-size="pageSize"
        @current-change="pageChange"
      >
      </el-pagination>
    </el-footer>
  </div>
</template>
<script>
  export default{
    name: "perSummary",
    data(){
      return{
        //分支id
        branchId:'',
        tableData:[],
        //筛选
        value:'',
        options:[],
        //弹框
        FormVisible: false,
        formData: {
          pic:''
        },
        disabled:true,
        dialogTableData:[],
        //分页器
        pageSize:5,
        page:1,
        total:5
      }
    },
    methods:{
      //获取分支名
      findBranchName(){
          this.$axios.get('getBranch').then( res => {
              this.options = res.data
//            console.log(this.options )
          })
      },
      //获取用户列表
      getUsers(){
        this.$axios.get('getUsers').then( res => {
          this.tableData = res.data
        })
      },
      //获取分支的用户列表
      getBranchUser(val){
        //重新获取分页总数
        this.branchId = val
        this.$axios.get('getUsersNum', {branchId:val}).then( res => {
          this.total = res.data
        });
        //获取分支用户
        this.$axios.get('getBranchUser', {branchId:val}).then( res => {
          this.tableData = res.data
        })
      },
      //编辑
      look(id){
//          console.log(id)
//          console.log(this.branchId)
//          console.log(this.$route.query.id)
        this.$axios.get('getPerSummary', {id, branchId:this.branchId, reviewId:this.$route.query.id}).then( res => {
            this.formData = res.data
            this.dialogTableData = res.data.common
        })
      },
      //删除一个
      del(id){
//          console.log(id)
        this.$confirm('此操作将永久删除该内容, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$axios.post('delPerSummary', {id}).then(res => {
            if(res.code == 200){
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
              setTimeout( () => {
                this.$router.go(0)
              },1000)
            }else{
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          })
        }).catch(() => {
          this.$message({
            type: 'warning',
            message: '已取消删除'
          });
        })
      },
      //底部分页器
      //这个时候利用formatter就可以实现索引累加啦
      formatter(index){
        //放回索引
        return index + (this.page -1) *this.pageSize +1
      },
      pageChange(val){
//          console.log(`当前页: ${val}`);
        this.page = val
        this.$axios.get('getUsers',{page:val, pageSize:this.pageSize}).then( res => {
//              setTimeout( () => {
//                this.$router.go(0)
//              },1000)
          this.tableData = res.data
        })
      },
      //获取数据库文档总数
      getNumber(){
        this.$axios.get('getUsersNum').then( res => {
          this.total = res.data
        })
      }
    },
    created(){
        this.findBranchName();
        this.getUsers();
        this.getNumber();
    }
  }
</script>
<style>
  .perSummary {
    width: 100%;
    padding-top: 2px;
    padding-left: 20px;
  }
  /*头部*/
  .perSummary .el-header{
    position: relative;
    height: 78px !important;   /*element.style里的样式需要用!important来修改*/
    margin-right: 22px;
    padding-left:11px;
    text-align: left;
    background-color: #f1f1f1;
  }
  .perSummary .el-header .el-input{
    width: 190px;
    margin-top: 10px;
  }
  .perSummary .el-header .el-input .el-input__inner{
    border:1px solid #c2cdda;
  }
  .perSummary .el-header .el-button{
    margin-left:14px;
  }
  .perSummary .el-header .el-dropdown{
    position:absolute;
    top:10px;
    right:10px;
    font-size:22px;
  }
  .perSummary .el-header .el-dropdown i{
    font-size:22px;
    margin:0 !important;
  }
  .perSummary .el-header .el-dropdown span{
    font-size:14px;
    line-height: 26px;
  }
  /*内容区*/
  .perSummary .el-main{
    margin:10px 22px 10px 0;
    padding: 0 14px;
    border:1px solid #dfe7ed;
  }
  .perSummary .el-table .has-gutter .cell{
    color:#000;
  }
  /*弹框*/
  /*表单上传*/
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  /*底部*/
  .perSummary .el-footer{
    /*position: fixed;*/
    /*min-height: 500px;*/
    /*bottom: 20px;*/
    /*right:20px;*/
    position:relative;
    height: 56px !important;
    margin:0 22px 0 0;
    padding:0;
    background-color: #f1f1f1 !important;
  }
  .perSummary .el-button{
    margin:10px 0 0 10px;
  }
  .perSummary .el-footer .el-pagination{
    display: inline-block;
    position:absolute;
    top:12px;
    right:15px;
  }
  .perSummary .el-footer .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
    background-color: #ffffff;
  }
</style>
<style scope>

</style>



